import { safeParse } from 'valibot';


export default {
  /**
   * éè¿å­ç¬¦ä¸²åå»ºèç¹
   * @param htmlStr
   * @returns {Element}
   */
  createNode(htmlStr) {
    const node = document.createElement('div')
    node.innerHTML = htmlStr
    return node.firstChild
  },

  //é²æ
  debounce(func, wait = 500) {
    let timeout

    return function () {
      const context = this
      const args = arguments

      clearTimeout(timeout)
      timeout = setTimeout(function () {
        func.apply(context, args)
      }, wait)
    }
  },

  // å¤æ­iframeæ¯å¦è·¨å
  canAccessIFrame(iframe) {
    if (!iframe.contentWindow || !iframe.contentDocument) {
      return false
    }
    return true
  },

  /**
   * ç»ä¸ä¸ªåç´ çæäºç¹å®åä»£åç´ è®¾ç½®å±æ§
   * @param {Element|String} element  éè¦è®¾ç½®æ ·å¼çåç´ ,å¯ä»¥æ¯domå¯¹è±¡ä¹å¯ä»¥æ¯domå­ç¬¦ä¸²
   * @param {Array} selectorArr
   */
  setProperty(element, selectorArr, name, value) {
    if (!Array.isArray(selectorArr)) {
      console.error('Invalid arguments')
      return
    }

    const type = !(element instanceof Element)

    if (type) {
      const tempElement = document.createElement('div')
      tempElement.innerHTML = element
      element = tempElement
    }

    element.querySelectorAll(selectorArr).forEach((tabBarItem) => {
      tabBarItem.style.setProperty(name, value)
    })

    return type ? element.innerHTML : element
  },

  sprintf(_str, ...args) {
    let flag = true
    let i = 0

    const str = _str.replace(/%s/g, () => {
      const arg = args[i++]

      if (typeof arg === 'undefined') {
        flag = false
        return ''
      }
      return arg
    })

    return flag ? str : ''
  },

  isStr(str) {
    return Object.prototype.toString.call(str) === '[object String]'
  },

  //æ°ç»å¯¹è±¡å»é
  arrUnique(arr, objKey) {
    //ä¸´æ¶æ°ç»
    let temp = []
    return arr.reduce(function (prev, curr) {
      if (temp.indexOf(curr[objKey]) === -1) {
        temp.push(curr[objKey])
        prev.push(curr)
      }
      return prev
    }, [])
  },

  // ç±»ä¼¼jQueryç$(document).ready(function () {});
  ready(callback) {
    if (document.readyState === 'loading') {
      document.addEventListener('DOMContentLoaded', callback)
    } else {
      callback()
    }
  },

  //å¯ä»¥è®©åå»äºä»¶å·å¤åå»çè½å
  handleSingleAndDoubleClick(
    callbacks,
    { enableDbClick = true, delay = 200 } = {},
  ) {
    let clicks = 0
    let timer = null
    const { click, dbclick } = callbacks

    return function (event) {
      const preventAndstop = (type) => {
        const { preventDefault = true, stopPropagation = true } = type
        if (preventDefault) event.preventDefault()
        if (stopPropagation) event.stopPropagation()
      }

      const callBack = (type) => {
        const { handle } = type
        if (typeof handle === 'function') {
          handle.call(this, event)
        }
      }

      if (enableDbClick === true) {
        clicks++
        if (clicks === 1) {
          //åå»

          preventAndstop(click)

          timer = setTimeout(() => {
            callBack(click)
            clicks = 0
          }, delay)
        } else {
          preventAndstop(dbclick)
          clearTimeout(timer)
          callBack(dbclick)
          clicks = 0
        }
      } else {
        preventAndstop(click)
        callBack(click)
      }
    }
  },

  extend() {
    let options,
      name,
      src,
      copy,
      copyIsArray,
      clone,
      target = arguments[0] || {}, //ç¬¬ä¸ä¸ªåæ°
      i = 1,
      length = arguments.length,
      deep = false

    // å¤çæ·±åº¦å¤å¶æåµ
    if (typeof target === 'boolean') {
      deep = target

      // è·³è¿å¸å°å¼åç®æ 
      target = arguments[i] || {}
      i++
    }

    // å½ç®æ æ¯å­ç¬¦ä¸²æå¶ä»ä¸è¥¿æ¶å¤çå¤§å°åï¼å¯è½å¨æ·±åº¦å¤å¶ä¸­ï¼
    if (typeof target !== 'object' && typeof target !== 'function') {
      target = {}
    }

    // å¦æåªä¼ éä¸ä¸ªåæ°ï¼åæ©å±jQueryæ¬èº«
    if (i === length) {
      target = this
      i--
    }

    for (; i < length; i++) {
      // ä»å¤çénull/æªå®ä¹çå¼
      if ((options = arguments[i]) != null) {
        // å»¶ä¼¸åºç¡å¯¹è±¡
        for (name in options) {
          copy = options[name]

          // é²æ­¢Object.prototypeæ±¡æ
          // é²æ­¢æ ä¼æ­¢çå¾ªç¯
          if (name === '__proto__' || target === copy) {
            continue
          }

          // å¦ææä»¬æ­£å¨åå¹¶æ®éå¯¹è±¡ææ°ç»ï¼åéå¤åºç°
          if (
            deep &&
            copy &&
            (this.isPlainObject(copy) || (copyIsArray = Array.isArray(copy)))
          ) {
            src = target[name]

            // ç¡®ä¿æºå¼çç±»åæ­£ç¡®
            if (copyIsArray && !Array.isArray(src)) {
              clone = []
            } else if (!copyIsArray && !this.isPlainObject(src)) {
              clone = {}
            } else {
              clone = src
            }
            copyIsArray = false

            // ä»ä¸ç§»å¨åå§å¯¹è±¡ï¼èæ¯åéå®ä»¬
            target[name] = this.extend(deep, clone, copy)

            // ä¸è¦å¼å¥æªå®ä¹çå¼
            // } else if (copy !== undefined) {
          } else {
            target[name] = copy
          }
        }
      }
    }

    // è¿åä¿®æ¹åçå¯¹è±¡
    return target
  },

  //ç¨äºå¤æ­ä¸ä¸ªå¯¹è±¡æ¯å¦æ¯çº¯ç²¹ç JavaScript å¯¹è±¡ï¼å³ä¸æ¯ DOM å¯¹è±¡ãå½æ°ãæ°ç»ç­ï¼ãå·ä½ä½ç¨æ¯æ£æ¥å¯¹è±¡æ¯å¦éè¿å¯¹è±¡å­é¢éæ new Object() åå»ºï¼ä¸å¶ååé¾ä¸åªåå«æ åç Object åå
  isPlainObject(obj) {
    if (typeof obj !== 'object' || obj === null || obj instanceof Array) {
      return false
    }

    const prototype = Object.getPrototypeOf(obj)
    return prototype === Object.prototype || prototype === null
  },

  isObject(value) {
    return Object.prototype.toString.call(value) === '[object Object]'
  },

  isJSONString(str) {
    try {
      const result = JSON.parse(str)
      return (
        Object.prototype.toString.call(result) === '[object Object]' ||
        Array.isArray(result)
      )
    } catch (e) {
      return false
    }
  },

  parseAttributeValue(value) {
    // å¦æå¼æ¯ 'true' æ 'false'ï¼è½¬æ¢ä¸ºç¸åºçå¸å°å¼
    if (value === 'true') {
      return true
    } else if (value === 'false') {
      return false
    }

    // å°è¯è§£æä¸º JSON
    if (this.isJSONString(value)) {
      return JSON.parse(value)
    }

    // å°è¯è·åå¨å±å½æ°
    const globalFunction = window[value]
    if (typeof globalFunction === 'function') {
      return globalFunction
    }

    // è¿ååå§å¼
    return value
  },


  validateOptions(struct, options) {
    const result = safeParse(struct, options, { abortEarly: true, abortPipeEarly: true });
    if (!result.success) {
      const formatErr = [];
      this.flattenErrorMessage(result.issues, formatErr)
      return formatErr[0];
    }
    return true;
  },

  flattenErrorMessage(errorArr, res = [], key = '') {
    errorArr.forEach(error => {
      if (error.path) {
        key += error.path.map(obj => obj.key).join('-');
        let forword = error.validation
        if (['union', 'enum'].includes(forword)) {
          forword = error.message
        }
        if (forword === "custom") {
          forword = error.message.type
        }
        res.unshift(`options [${key}] : ææç±»åæå¼æ¯${forword},ä½æ¥æ¶çæ¯${error.path.reverse()[0].value}`)
        const errs = error.issues;
        if (Array.isArray(errs) && errs.length > 0) {
          this.flattenErrorMessage(errs, res, key + '-')
        }
      }
    })
    return res;
  }
}
